import { fetchEndpoint, fetchEndpointBlob } from '../server/network'

/**
 * Gets the push list.
 * 查询push管理list
 *
 * @param      {Number}  params.page  页数
 * @param      {Number}  params.size  每页数据量
 * @param      {String}  params.title     标题
 * @param      {Number}  params.start     开始时间
 * @param      {Number}  params.end       结束时间
 * @param      {Number}  params.exe_code  状态 查询全部时不传
 */
export const getPushList = params => {
  const o = {
    page: params.page,
    size: params.size,
    title: params.title,
    exe_time_start: params.start,
    exe_time_end: params.end
  }
  if (typeof params.exe_code == 'number') {
    o.exe_code = params.exe_code
  }
  return fetchEndpoint('/admin/api.app.push.list', 'POST', o)
}

// 删除某条push管理
export const delPushRecord = id => fetchEndpoint('/admin/api.app.push.delete', 'POST', { id })

// 获取push详情
export const getPushDetail = id => fetchEndpoint('/admin/api.app.push.info', 'POST', { id })

/**
 * 保存push管理数据
 *
 * @param      {Number}  params.id  标题
 * @param      {String}  params.title  内容
 * @param      {String}  params.content  跳转网址
 * @param      {String}  params.go_url  消息发送类型：0=部分发送，1=全部发送
 * @param      {Number}  params.push_type  文件地址（push_type=0时，必传）
 * @param      {String}  params.file_path  发送时间类型：0=立即，1=定时
 * @param      {Number}  params.push_time_type  发送时间（push_time_type=1时，必传）
 * @param      {Number}  params.plan_exe_time  新增不传，编辑必传
 */
export const editPushRecord = params => fetchEndpoint('/admin/api.app.push.save', 'POST', params)

// 下载手机号模板
export const downloadTemp = _ => fetchEndpointBlob('/admin/api.app.push.down.template', 'POST', {})