<template>
  <div class="container">
    <page-title first-title="push管理" :second-title="id ? '编辑' : '新增'"></page-title>
    <div class="foem-wrapper">
      <el-form :model="form" :rules="rules" ref="form" v-loading="loading" label-width="150px" :disabled="isCheck">
        <el-form-item label="标题" prop="title">
          <el-input v-model="form.title"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content">
          <el-input class="text-area" type="textarea" v-model="form.content"></el-input>
        </el-form-item>
        <el-form-item label="目标人群" prop="push_type">
          <el-radio-group v-model="form.push_type" @change="pushType">
            <el-radio :label="false">指定用户</el-radio>
            <el-radio :label="true">全部用户</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="!form.push_type" label="用户列表" prop="file_path" required>
          <el-upload
            :action="uploadUrl"
            :headers="headers"
            name="myfile"
            accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            :on-success="uploadSuccess"
            :show-file-list="false"
            :with-credentials="true"
          >
            <el-button slot="trigger" type="primary" size="small" icon="el-icon-upload2">批量导入</el-button>
          </el-upload>
          <el-button type="text" size="small" @click="download">下载模板</el-button>
          <p class="upload-count" v-if="uploadCount !== undefined">已导入用户数：{{ uploadCount }}</p>
        </el-form-item>
        <el-form-item label="发送时间" prop="push_time_type">
          <el-radio-group v-model="push_time_type" @change="timeTypeChange">
            <el-radio :label="true">定时发送</el-radio>
            <el-radio :label="false">即时发送</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="push_time_type" label="定时发送时间" prop="plan_exe_time">
          <el-date-picker v-model="form.plan_exe_time" type="datetime" placeholder="请选择发送时间"> </el-date-picker>
        </el-form-item>
        <el-form-item label="链接地址" prop="go_url">
          <el-input v-model="form.go_url"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">立即创建</el-button>
          <el-button @click="goBack">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { saveAs } from 'file-saver'
import { getPushDetail, editPushRecord, downloadTemp } from '../../api/push'
export default {
  name: 'pushEdit',
  data() {
    let validSendTime = (rule, value, callback) => {
      if (this.push_time_type) {
        if (!value) {
          return callback(new Error('请选择发送时间'))
        }
        const now = new Date().getTime()
        const select = value.getTime()
        if (select < now) {
          return callback(new Error('定时发送时间不得早于当前时间'))
        }
      }
      return callback()
    }
    let validFile = (rule, value, callback) => {
      if (!this.push_type && !this.filePath) {
        return callback(new Error('请上传用户手机号文件'))
      }
      return callback()
    }
    return {
      id: this.$route.query.id,
      isCheck: !!this.$route.query.isCheck,
      form: {
        title: '',
        content: '',
        plan_exe_time: '',
        push_type: false,
        file_path: '',
        go_url: ''
      },
      loading: false,
      push_time_type: false,
      uploadUrl: `${process.env.VUE_APP_APPSERVER_HOST}/admin/api.app.push.lead`,
      filePath: '',
      uploadCount: undefined,
      rules: {
        title: [
          { required: true, message: '请输入标题', trigger: 'blur' },
          { max: 15, message: '长度不超过15个字符', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '请填写推送内容', trigger: 'blur' },
          { max: 100, message: '长度不超过100个字符', trigger: 'blur' }
        ],
        go_url: [{ required: true, message: '请填写链接地址', trigger: 'blur' }],
        plan_exe_time: [
          { required: true, message: '请选择推送时间', trigger: 'blur' },
          { validator: validSendTime, trigger: 'blur' }
        ],
        file_path: [{ validator: validFile, message: '指定人群发送必须上传用户手机号', trigger: 'blur' }]
      }
    }
  },

  computed: {
    headers() {
      let token = ''
      const platform_token =
        'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1aWQiOjIsImV4cCI6Mjg0MDExMjAwMCwicGxhdGZvcm0iOiIifQ.x7sXrFHcDZUEiIkVq2JDO1FQxpOsXXsq0qTaAKuGQNw'
      token = window.token
      if (process.env.NODE_ENV === 'development') {
        token = platform_token
      }
      return {
        'X-ADMIN-Authorization': token,
        'X-HH-Ver': '0.4.3'
      }
    }
  },

  created() {
    if (this.id) {
      this.getDetail()
    }
  },

  methods: {
    onSubmit() {
      this.$refs['form'].validate(valid => {
        if (valid) {
          const params = {
            title: this.form.title,
            content: this.form.content,
            go_url: this.form.go_url,
            push_type: this.form.push_type ? 1 : 0,
            push_time_type: this.push_time_type ? 1 : 0
          }
          if (this.id) {
            params.id = this.id
          }
          if (!this.form.push_type) {
            params.file_path = this.filePath
          }
          if (this.push_time_type) {
            params.plan_exe_time = parseInt(this.form.plan_exe_time.getTime() / 1e3)
          }
          this.saveRecord(params)
        } else {
          return false
        }
      })
    },

    saveRecord(params) {
      editPushRecord(params).then(res => {
        this.$router.go(-1)
      })
    },

    getDetail() {
      this.loading = true
      getPushDetail(this.id).then(res => {
        this.loading = false
        this.form = { ...res }
        this.form.plan_exe_time = this.form.plan_exe_time ? new Date(this.form.plan_exe_time * 1e3) : new Date()
        this.form.push_type = this.form.push_type == 1
        this.push_time_type = !!this.form.push_time_type
      })
    },

    pushType(val) {
      this.uploadCount = undefined
      this.filePath = ''
    },

    timeTypeChange(val) {
      this.form.plan_exe_time = ''
    },

    uploadSuccess(res, file, fileList) {
      if (res.code == 0) {
        this.uploadCount = res.data.count
        this.filePath = res.data.file_path
      }
    },

    goBack() {
      this.$router.go(-1)
    },

    download() {
      downloadTemp().then(res => {
        saveAs(new Blob([res.data]), res.filename || 'app_push.xls')
      })
    }
  },

  watch: {
    filePath(val) {
      this.form.file_path = val
    }
  }
}
</script>

<style lang="scss" scoped>
.foem-wrapper {
  width: 700px;
  margin: 25px auto 0;
}
.upload-count {
  line-height: 1.5;
  margin: 0;
  color: #cccccc;
}
.foem-wrapper /deep/ input {
  font-family: '微软雅黑';
}
.text-area /deep/ textarea {
  font-family: '微软雅黑';
}
</style>
